@charset "UTF-8";

/* _page_widget.scss */

.widget{display:block;position:absolute}
.widget .background{position:absolute;top:0;left:0;width:100%;height:100%}
.widget .text_wrap,
.widget .text_wrap .text{display:none;}

.widget_shape{
  overflow:hidden;

  .svg_shape{
    position:absolute;
    top:0;
    left:0;
  }

  .svg_adaptive_shape{
    display:none
  }

  .text_wrap {
    display: block;
    position: absolute;
    top: 0;
    left: 5px;
    right: 5px;
    bottom: 0;

    .text_cont{
      overflow:hidden;
      display:table;
      table-layout:fixed;
      width:100%;
      height:100%;

      .text{
        display:table-cell;
        word-break:break-word;
      }
    }
  }
}

.widget_HotSpot {
  background:rgba(255,255,255, 0.0);
}

.widget_HamburgerMenu{
  cursor:pointer;
}

.group{
  position:absolute
}

.widget_radioButton input,
.widget_checkbox input{
  position:absolute;
  left: 0;
  top: 2px;
  width: auto;
  height: auto;
  margin: 0;
  padding:1px 0;
}

.widget_radioButton label,
.widget_checkbox label{
  display:inherit;
  position: absolute;
  top:0;
}

.widget_listBox select,
.widget_dropList select{
  display:inherit;
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
}

.widget_Button input,
.widget_textArea textarea,
.widget_textField input{
  box-sizing:border-box;
}
.widget_textArea textarea,
.widget_textField input{
  border:1px solid #bbb;
}

.widget_textArea textarea:disabled,
.widget_textField input:disabled{
  color:#808080 !important;
}

.widget_textField input[type="file"]{
  border:0;
}

.widget_textArea textarea{
  resize:none;
}

.widget_Image img{width:100%;height:100%}

.hamburgerMenu{
  position: absolute;
  display: none;
}

.hamburgerMenu .hamburgerMenuContent{
  position:relative;top:0;left:0;width:100%;height:100%;
}

.hamburgerBackground {
  display:none;
  position: fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  background-color:#000;
  opacity:.6;
  cursor:pointer;
}

.widget_toast{
  display:none;
  overflow:hidden;
}
.widget_toast .btn_close{
  position:absolute;
  top:0;
  right:0;
  width:26px;
  height:26px;
  z-index:9999;
  border:0;
  background:url('../images/widget/btn_close_widget.png') no-repeat 50% 50%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor:pointer;
}

.widget_Scroll {
  overflow:hidden;
  -webkit-overflow-scrolling: touch;

  .scroll-container{
    height:100%;
  }
  .scroll-ct {
    overflow: hidden;
    position: relative;
    float: left;
  }
}

.widget_Swipe .slick-slide-content{
  position:relative;
  overflow:hidden;
  height:100%;
}
