@charset "UTF-8";

/* _page.scss */

.pc #viewerPageContent{
  position: relative;
  font-family:'나눔고딕',nanumgothic,'돋움',Dotum,Helvetica,'Apple SD Gothic Neo',Sans-serif;
}

/* link interaction, widget note */
.note_highlight:before,
.link_highlight:before {
  position:absolute;
  left:-2px;
  right:-2px;
  top:-2px;
  bottom:-2px;
  background:rgba(229,244,249,.5);
  content:'';
  z-index:10;
  -webkit-transition: all 1s ease-out;
  transition: all 1s ease-out;
}

.note_highlight:after,
.link_highlight:after {
  @include box-shadow(3px, 3px, 3px, rgba(0,0,0,0.2));

  position:absolute;
  left:-4px;
  right:-4px;
  top:-4px;
  bottom:-4px;
  border:1px solid #bbe4f6;
  content:'';
  z-index:-1;

  -webkit-transition: all 1s ease-out;
  transition: all 1s ease-out;
}

.note_highlight.fade_out:before,
.link_highlight.fade_out:before{
  opacity: 0;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}

.note_highlight.fade_out:after,
.link_highlight.fade_out:after{
  @include box-shadow(3px, 3px, 3px, rgba(0,0,0,0.2));

  opacity:.5;
}

/* 인터렉티브, 링크 위젯 하이라이팅 */
.link_highlight:before{
  background:rgba(14,92,255,.6);
}
.link_highlight:after{
  border-color: #284a95;
}
